module.exports = {"main":{"name":"Lee M. Raulin","description":"Web developer specializing in JavaScript (React, Node, Express) and Python. I use code to solve problems.","image":"profilepic.jpg","bio":["After a previous life as an English teacher in Korea, a stint in the Coast Guard, and an Master's in psychology, I finally found my calling as a programmer.","It began with using mods for Oblivion, when I discovered I found the process of troublshooting addictive. From there, I began using Linux as my primary OS, and the rest is history. In the last several years, I have been delighted to discover that I can use my skills to solve real problems in the worlds of business and government.","I'm confident I could build a REST API in any language at this point, but JavaScript (React, Express, Node...any database) is where I am most comfortable. Eric Elliot, Douglas Crockford, and Kyle Simpson convinced me that it is not only as good as any general-purpose programming language for most purposes; it is almost beautiful in its own way.","Python was my first language, and still second favorite. I'm also pretty handy with Bash, and recieved 3 months of formal training each in C# and Java.","My personality could be described as dopaminergic. My brain is ravenous and restless. I've sometimes felt as if I want to devour the world.","I am fascinated by psychology and neuroscience, especially their practical relevance for self-understanding and better living. I keep my body and brain and healthy with light hygeine, self-generated optic flow (ie walking and running), calisthenics, club swinging, yoga, breathwork, and daily ice baths in the Chesapeake Bay."],"contactMessage":"Get in touch!","email":"leeraulin@gmail.com","phone":"(443) 684-8939","github":"https://github.com/lraulin","project":"https://github.com/nordicgiant2/react-nice-resume","address":{"street":"","city":"Chesapeake Beach","state":"Maryland","zip":""},"website":"","resumeDownload":"https://www.dropbox.com/s/ngrqu4udpblvatn/Lee_Raulin_Resume.PDF?dl=0","social":[{"name":"linkedin","url":"https://www.linkedin.com/in/leeraulin/","className":"fa fa-linkedin"},{"name":"instagram","url":"http://instagram.com/tbaker_x","className":"fa fa-instagram"},{"name":"github","url":"https://github.com/lraulin","className":"fa fa-github"}]},"resume":{"skillmessage":"","education":[{"school":"Northcentral University","degree":"Masters in Psychology","graduated":"November 2013","description":"Describe your experience at school, what you learned, what useful skills you have acquired etc."},{"school":"St. Mary's College of Maryland","degree":"Batchelors in Psychology","graduated":"June 2004","description":"Describe your experience at school, what you learned, what useful skills you have acquired etc."}],"work":[{"company":"Snapnurse","title":"Booker","years":"November 2020 - Present","description":"Doing my part during COVID by helping nurses on the front lines get paid. Although my official job is approving timesheets, my programming skills have been in high demand. I created a React/Python/Flask webapp to help my team calculate holiday pay adjustments and to identify timecards needing admin approval. It is used by myself and several of my coworkers to speed our work on a daily basis. I have been developing my Pandas skills to perform data cleaning on spreadsheets. And I created my own React front end that uses's the company's backend API, in order to prototype UI enhancements that will save time and reduce errors for my team."},{"company":"Smoothstack","title":"Software Engineer","years":"December 2019 - September 2020","description":"Trained in developing microservices with Java Spring Boot, React/Angular, CI/CD with Jenkins. We worked on small teams created using Agile methodology, creating full stack applications from scratch. Furloughed due to COVID.","bullets":["GC Fashion Store: React, multiple microservices in Java/Spring Boot, Node/Express, and AWS Lambda.","Library Management System: Angular, Java/Spring Boot."]},{"company":"Workforce Opportunity Services (contractor at US Department of Transportation)","title":"Full Stack Web Developer","years":"September 2018 - September 2019","description":""}],"skills":[{"name":"JavaScript","level":"90%"},{"name":"ReactJs","level":"80%"},{"name":"NodeJS","level":"75%"},{"name":"Python","level":"70%"},{"name":"JAVA","level":"40%"},{"name":"C#","level":"40%"}]},"portfolio":{"projects":[{"title":"Incident Report Map","problem":"Boss had a pilot project he created for the Office of Crisis Management, which used the Twitter API to find reports of transportation-related incidents and plot them on a map, with markers filterable by date, category, etc. The code was outdated and the database was inaccessible, so he asked if I could rebuild it.","solution":"Rebuilt app from scratch using a PERN stack (PostgreSQL, Express, React, Node).","outcome":"App worked with full functionality of original. I submitted a budget proposal for deployment to AWS, which was accepted, but my contract ended before we were approved to proceed to deployment.","githubURL":""},{"title":"DNS Monitoring","problem":"Hundreds of DNS servers needed to be checked daily for record modifications, which might indicate malicious tampering, in compliance with security policy. Coworkers in the cyber-security department asked for my help in creating a script.","solution":"Wrote a script with NodeJS to execute dig command, parse and store the result, and compare the result to previous run. Deployed to server to execute hourly via cron, and send a consolidated report once per day.","outcome":"Script worked as intended. Cyber Security team was pleased. Script could still be running daily as far as I know.","githubURL":""},{"title":"Holiday Pay","problem":"Boss asked for script to calculate holiday pay adjustments, but boss is not a programmer.","solution":"Created Python script to calculate holiday pay adjustments. Turned into web app to make easier to use, by deploying script as Flask app, and creating React front end to upload input CSV and display output.","outcome":"Enabled team to enter holiday pay adjustments in less time with fewer errors. Added features to assist with daily tasks (display list of entries requiring super admin approval), Several coworkers and I continue to use it daily to speed our workflow.","githubURL":""}]}}

